










import Vue from 'vue';
export default {
	props: {
		food: Object
	},
	data() {
		return {
			add_icon: '../../../static/shop/icon_add.png',
			minus_icon: '../../../static/shop/icon_minus.png'
		}
	},
	methods: {
		addCart(event) {
			if (!this.food.count) {
				Vue.set(this.food, 'count', 1); // /
			} else {
				this.food.count++;
			};
			this.$emit('add', event.target); // @
		},
		decreaseCart() {
			if (this.food.count) {
				this.food.count--;
			}
		}
	}

}
